import React, {useState} from "react"
import { Location } from "@reach/router"
import { Wrapper, Welcome, BottomMonitor} from "./style"
import Projects from "../Projects"
import About from "../About"
import useMediaQuery from "../../hooks/useMediaQuery"

export default function Intro () {
    const [hash, setHash] = useState(0);
    const matches = useMediaQuery('(min-width: 800px)');
    return (
        <Wrapper>
            <Location>
                {({ location }) => { 
                    setHash(location.hash)
                    }}
            </Location>
            <Welcome>
                <p>Hi, I am</p>
                <h1>
                    Bella Rose
                </h1>
                <h2>
                    Aspiring data scientist, machine learning and natural language processing technology enthusiast
                </h2>
            </Welcome>
            {matches?
                <>
                {hash === "#about" ? <RenderAbout /> : <RenderProject/>}
                </>
                :
                <>
                    <RenderAbout />
                    <RenderProject/>
                </>
            }
            
        </Wrapper>
    )
}

function RenderProject () {
    return (
        <div>
            <Projects />
            <BottomMonitor />
        </div>
    )
}

function RenderAbout () {
    return (
        <div>
            <About />
            <BottomMonitor />
        </div>
    )
}