import React from "react"
import { ProjectContainer, Info, Wrapper, InnerWrapper, ProjectTitle, ProjectImg, ProjectDescription, Languages, LanguageItem, Icon, Links } from "./style"
import Card from "../../common/Card"
import { graphql, useStaticQuery } from "gatsby"
import GitHub from "../../SVG/Github"
import ArrowOut from "../../SVG/arrow-out"
import Container from "../../common/Container"

export default function Projects() {
    const {
        github: {
            viewer: {
                starredRepositories: { edges },
            },
        },
    } = useStaticQuery(
        graphql`
        {
            github {
                viewer {
                  starredRepositories(
                    first: 10
                    orderBy: {field: STARRED_AT, direction: DESC}
                    ) {
                        edges {
                        node {
                            id
                            name
                            url
                            languages(first: 10, orderBy: {field: SIZE, direction: DESC}) {
                            edges {
                                node {
                                name
                                id
                                color
                                }
                                size
                            }
                            }
                            shortDescriptionHTML
                            openGraphImageUrl
                            homepageUrl
                        }
                        }
                        totalCount
                    }
                }
            }
        }
        `
    );
    return (
        <Wrapper as={Container} id="projects">
            <InnerWrapper >
                <Info>
                    <h3>Completed Projects</h3>
                </Info>
                <ProjectContainer>
                {edges.map(({ node }) => (
                    <ProjectCard
                        id={node.id}
                        url={node.url}
                        homepageUrl={node.homepageUrl}
                        title={node.name}
                        desc={node.shortDescriptionHTML}
                        languages={node.languages}
                        img={node.openGraphImageUrl}
                    />
                ))}
                </ProjectContainer>
                <Info>
                    <h3>In-Progress Projects</h3>
                </Info>
                <ProjectContainer>
                    <Card>
                        <ProjectTitle>
                            Question-Answer on English Reading Paper
                        </ProjectTitle>
                        <ProjectDescription>
                            a supervised machine learning program to answer english passage
                        </ProjectDescription>
                    </Card>
                    <Card>
                        <ProjectTitle>
                            What am I doing with my life?
                        </ProjectTitle>
                        <ProjectDescription>
                            a program to predict a "highlight" from a selected day, week or month, using data
                            from location history and calendar events.
                        </ProjectDescription>
                    </Card>
                </ProjectContainer>
            </InnerWrapper>
        </Wrapper>
    )
}

const ProjectCard = props => (
    <Card key={props.id} as="a" href={props.homepageUrl === "" ? props.url : props.homepageUrl}>
        <ProjectTitle>
            {props.title}
        </ProjectTitle>
        <ProjectImg src={props.img} alt={props.title}/>
        <ProjectDescription>
            {props.desc}
        </ProjectDescription>
        <Languages>
            {props.languages.edges.map(({ node }) => (
                // <LanguageItem key={node.id} color={node.color}>
                <LanguageItem key={node.id}>
                    {node.name}
                </LanguageItem>
            ))}
        </Languages>
        <Links>
            <Icon>
                <a href= {props.url === "" ? "https://github.com/bellaroseee/" : props.url}>
                    <GitHub/>
                </a>
            </Icon>
            <Icon>
                <a href={props.homepageUrl === "" ? props.url : props.homepageUrl}>
                    <ArrowOut/>
                </a>
            </Icon>
        </Links>
    </Card>
)